import styled, { keyframes } from 'styled-components';

//media
import { media } from '../media';
import MovingBG from '../assets/header-back.svg';

//animations
const scrollAnim = keyframes`
    from{
        transform: translate(19px, 27px);
        z-index: 5;
    }
    to{
        transform: translate(19px, 59px);
    }
`;

//globals
const section = `
    padding: 4em 2em;
    text-align: center;
`;

const subtitle = `
    text-transform: uppercase;
    font-weight: bold;
    color: #2ab9f1;
    letter-spacing: .2em;
    font-size: .85em
`;

const featuredTitle = `
    color: black;
    font-weight: bold;
    font-size: 1.3em;
    margin-top: -.4em;
    display: block 
`;

const featuredDesc = `
    color: #3B3B3B;
    margin-bottom: 3em;
    font-size: .9em;
    line-height: 1.8em;
    font-weight: 500;
`;

const tabletText = `
    text-align: left;
`;

const tabletContainer = `
    display: grid;
    grid-template-columns: 40% auto;
`;

const tabletLeft = `
    display: grid;
    place-content: center;
`;

const tabletRight = `
    margin-left: 2em;
    margin-top: 1em;
`;

const laptopSecions = `
    padding: 4em;
`

const desktopSecions = `
    padding: 10em 10em 4em 10em;
`;

const wideSections = `
    padding: 6em 20em 4em 20em;
`;

//Navbar Section
export const NavConatainer = styled.div`
    font-size: smaller;
    background: #595987;

    a{
        color: white;
    }

    header{
        display: flex;
        justify-content: space-between;
        padding: 2em;

        ul{
            display: flex;

            a{
                display: block;
                padding: 0 1em;
            }
        }
    }

    .social-header{
        display: none;
    }

    @media ${media.laptop}{
        font-size: medium;
        display: grid;
        grid-template-columns: 66% auto;
        background: #f4f4f4;

        header{
            background: #595987;
            padding: 2em 2em 2em 4em;
        }

        .social-header{
            padding: 2em 4em 2em 0;
            display: block;
            ul{
                display: flex;
                justify-content: space-between;
                width: 7em;
                float: right;

                img{
                    width: 20px;
                }
            }
        }
    }

    @media ${media.laptopL}{
        header{
            padding: 2em 2em 2em 10em;
        }

        .social-header{
            padding: 2em 10em 2em 0;
        }
    }

    @media (min-width: 1550px){
        header{
            padding: 2em 2em 2em 20em;
        }

        .social-header{
            padding: 2em 20em 2em 0;
        }
    }
`;

export const Logo = styled.a`
    color: #ffe691;
    font-weight: bold;
`;

//Intro Section
export const Hero = styled.div`
    font-size: smaller;
    background: #585887;
    color: white;
    display: grid;
    text-align: center;
    padding: 4em;

    .hero-design{
        display: none;
    }

    .meet{
        display: flex;
        margin: 0 auto;
        width: 200px;
        font-weight: bold;
        color: #ffe691
    }

    .circle{
        animation: ${scrollAnim} .75s infinite alternate-reverse;
    }

    #scroll{
        width: 30px;
    }

    @media ${media.laptop}{
        font-size: normal;
        height: 90vh;
        display: grid;
        grid-template-columns: 66% auto;
        background: #f4f4f4;
        padding: 0;

        .content{
            background: #585887;
            padding: 6em 8em 6em 4em;
            text-align: left;

            h1{
                font-size: 3em;
                line-height: 1.2em;
            }
        }

        .meet{
            margin: unset;
            font-size: large;
        }

        .hero-design{
            display: unset;
            margin-left: -25%;
            margin-top: 15%;
            width: 100%;
        }
    }

    @media ${media.laptopL}{
        & .content{
            padding: 6em 8em 6em 10em;
        }
    }

    @media (min-width: 1550px){
        & .content{
            padding: 9em 20em 6em 20em;
        }
    }
`;

//Content Secion
export const AboutMe = styled.section`
    font-size: smaller;
    background: #585898;
    color: white;
    display: flex;
    flex-flow: column-reverse;
    text-align: center;
    padding: 4em;

    .profile-pic{
        img{
            border-radius: 50%;
            @media (min-width: 426px){
                width: 70%;
            }
        }
    } 

    .about-content{
        text-align: left;

        h3{
            padding-top: .5em;
        }

        h2{
            padding-top: 1em;
            font-weight: bold;
        }

        p{
            ${featuredDesc}
            color: white;
            margin: 0;
        }

        li{
            list-style-type: disc;
            ${featuredDesc}
            color: white;
            margin: 0;
        }
    }

    @media ${media.tablet}{
        font-size: normal;
        display: grid;
        grid-template-columns: 66% auto;
        padding: 0;

        .about-content{
            padding: 4em 6em 4em 4em;
        }

        .profile-pic{
            display: grid;
            place-content: center;
            img{
                margin-left: -15%;
                width: 100%;
            }
        }
    }

    @media ${media.laptop}{
        background: #f4f4f4;

        h2{
            font-size: 3em;
        }

        h3, h4{
            font-size: 2em;
        }

        .about-content{
            padding: 6em 8em 6em 4em;
            display: grid;
            place-content: center;
            &,p,li{
                color: #000;
            }
            p,li{
                font-size: 1.25em;
            }
        }
        
        .profile-pic{
            background: url(${MovingBG}) no-repeat;
            background-size: cover;
            img{
                margin: unset;
            }
        }
    }

    @media ${media.laptopL}{

        & .about-content{
            padding: 9em 15em 6em 15em;
        }
    }
`;

export const Featured = styled.section`
    ${section}
    position: relative;

    &::before{
        content: "";
        position: absolute;
        width: 200px;
        height: 200px;
        border-radius: 50%;
        background: #eec6cc;
        top: 0;
        left: 0;
        z-index: -1;
    }
    
    img{
        border-radius: 1em;
    }

    .subtitle{
        ${subtitle}
    }

    .featured-title{
        ${featuredTitle}
    }

    .featured-desc{
        ${featuredDesc}
    }

    .kollegin{
        margin-bottom: 70px;
        display: flex;
        img{
            margin-right: 15px;
            background-color: #eec6cc;
            border-radius: 50%;
            width: 50px;
        }
    }

    @media ${media.tablet}{
        ${tabletText}
        ${tabletContainer}

        .left{
            ${tabletLeft}
        }

        .right{
            ${tabletRight}
        }
    }

    @media ${media.laptop}{
        ${laptopSecions}
    }

    @media ${media.laptopL}{
        ${desktopSecions}
    }

    @media (min-width: 1550px){
        ${wideSections}
    }
`;

export const Skills = styled.section`
    ${section}
    background: #ffa6bb;

    .featured-desc{
        ${featuredDesc}
        margin-bottom: 2em;
    }

    .skills-container ul li{
        background: white;
        padding: 2em;
        border-radius: 1em;
        margin-bottom: 1em;
    }

    .icon-container{
        height: 100px;
        display: grid;
        place-content: center;
        margin: 0 auto;

        &.icon-size{
            width: 80px;
        }
    }

    .skill-title{
        font-weight: bold;
    }

    @media ${media.tablet}{
        margin-top: -5em;
        margin-bottom: -7em;
        padding-top: 7em;

        .skills-container ul{
            display: grid;
            grid-template-columns: repeat(3, auto);
            grid-gap: 1em;
        }
    }

    @media ${media.laptop}{
        ${laptopSecions}
    }

    @media ${media.laptopL}{
        ${desktopSecions}
    }

    @media (min-width: 1550px){
        ${wideSections}
    }
`;

export const Portfolio = styled.section`
    ${section}
    background: #f7f7f7;
    a img{
        border-radius: 1em;
        margin-bottom: 2em;
    }

    .subtitle{
        ${subtitle}
    }

    .featured-title{
        ${featuredTitle}
    }

    .featured-desc{
        ${featuredDesc}
    }

    @media ${media.tablet}{
        padding-top: 7em;

        .portfolio-container{
            ${tabletContainer}
        }

        .portfolio-container img{
            margin-left: 2em;
        }

        .portfolio-left{
            ${tabletLeft}
        }

        .right{
           ${tabletRight}
        }
    }

    @media ${media.laptop}{
        ${laptopSecions}

        img{
            float: right;
            max-width: 500px;
        }
    }

    @media ${media.laptopL}{
        ${desktopSecions}
    }

    @media (min-width: 1550px){
        ${wideSections}
    }
`;

export const ContactMe = styled.section`
    ${section}
    background: #fff1c0;

    .subtitle{
        ${subtitle}
    }

    .featured-title{
        ${featuredTitle}
    }

    .featured-desc{
        ${featuredDesc}
        text-decoration: underline;
    }

    .social-footer{
        
        ul{
            display: flex;
            justify-content: space-around;
            align-self: center;
            img{
                width: 40px;
            }
        }
    }

    @media ${media.tablet}{
        margin-top: -7em;
        margin-bottom: -10em;
        padding-top: 5em;
    }

    @media ${media.laptop}{
        ${laptopSecions}
    }

    @media ${media.laptopL}{
        ${desktopSecions}
    }

    @media (min-width: 1550px){
        ${wideSections}
    }
`;